{ IGetExportSelectionData } from \"../../../../domain/useCase/IGetExportSelectionData\";\nimport { IRouter } from \"../../../../domain/presentation/IRouter\";\nimport ExportApp from \"../../components/application/exportApp.vue\";\nimport LivePreview from \"../../components/application/livePreview.vue\";\nimport { ILocalization } from \"../../../../domain/infrastructure/ILocalization\";\nimport { ExportType, IExportJob } from \"@stechquick/algae/lib/qCloudTemp/exporter\";\nimport { ILogout } from \"../../../../domain/useCase/ILogout\";\nimport { TooltipImpl } from \"../../impls/tooltipImpl\";\nimport { ITimer } from \"../../../../domain/infrastructure/ITimer\";\nimport RadioButtonComp from \"../dialogs/preview/components/radioButtonComp.vue\";\nimport { IAppLivePreviewParameters } from '@stechquick/algae/lib/qCloudTemp/quickCloud';\nimport { IUpdateApplication } from '../../../../domain/useCase/IUpdateApplication';\nimport { ILivePreviewUseCase } from \"../../../../domain/useCase/ILivePreviewUseCase\";\nimport { ICustomLivePreview } from '@stechquick/algae/lib/qCloudTemp/livePreview';\nimport { IApplicationSettings } from \"../../../../domain/useCase/IApplicationSettings\";\n\nconst viewModel = container.resolve<IViewModel>(IViewModel);\nconst executor = container.resolve<IUseCaseExecutor>(IUseCaseExecutor);\nconst studio = container.resolve<IStudio>(IStudio);\nconst notification = container.resolve<INotification>(INotification);\nconst dialog = container.resolve<IDialog>(IDialog);\nconst livePreviwUseCase = container.resolve<ILivePreviewUseCase>(ILivePreviewUseCase);\nconst appSettingsCase = container.resolve<IApplicationSettings>(IApplicationSettings);  \nconst selectionData = ref([] as unknown as Array<IExportViewItem>);\nconst router = container.resolve<IRouter>(IRouter);\nconst localization = container.resolve<ILocalization>(ILocalization);\nconst timer = container.resolve<ITimer>(ITimer);\nconst routerParams = router.getRouteParams<{ appID: string }>();\nconst checkedOut = computed(() => viewModel.studio.currentItem?.checkedOut);\nconst props = defineProps<{ app: IApplication }>();\nconst deployTip = ref(\"\");\nconst deployTooltipVisbile = ref(false);\nconst deployContentOpacity = ref(1);\nconst hoverValue = ref(false);\nconst tooltip = container.resolve<TooltipImpl>(TooltipImpl);\nconst previewType = computed(() => viewModel.apps.find(app => app.ID == viewModel.studio.appId)?.livePreviewParameters?.selectedType || \"default\");\nconst showCustomPreview = computed(() => viewModel.apps.find(app => app.ID == viewModel.studio.appId)?.livePreviewParameters?.livePreviews.customID);\nconst customPreviewLastUpdated = ref();\nwatch(showCustomPreview, () => {\n  if (showCustomPreview.value) {\n    executor.execute(\n      async () => {\n        const livePreviewInfo = await livePreviwUseCase.getCustomLivePreviewInfo(viewModel.studio.appId);\n        const date = new Date((livePreviewInfo as ICustomLivePreview).creationInfo.date)\n        customPreviewLastUpdated.value = date.getDate() + \".\" + (date.getMonth() + 1) + \".\" + date.getFullYear() + \" \" + (date.getHours() < 10 ? \"0\" : \"\") + date.getHours() + \":\" + (date.getMinutes() < 10 ? \"0\" : \"\") + date.getMinutes();\n      },\n      { loading: true }\n    );\n  }\n})\n\nfunction getLivePreviewID(): string | undefined {\n  const curApp = viewModel.apps.find(i => i.ID == viewModel.studio.appId)!;\n  const lpParams = curApp.livePreviewParameters;\n  if (!lpParams) { return undefined; }\n\n  const lpID = lpParams.selectedType == \"default\" ? lpParams.livePreviews.defaultID! : lpParams.livePreviews.customID!;\n  return lpID;\n}\n\nconst tablist = computed(() => {\n  return (\n    viewModel.studio?.openItems?.map((item) => {\n      const retVal: ITab = {\n        name: item.name,\n        type: item.modelType,\n        id: item.ID,\n        selected: viewModel.studio.currentItem?.ID == item.ID,\n        modified: item.modified?.model,\n        synced: item.synced,\n        cb: {\n          close: executor.wrap(async () => studio.closeItem(item), { loading: false }),\n          select: executor.wrap(async () => studio.openItem(item), { loading: true }),\n        },\n      };\n      return ref(retVal).value;\n    }) || []\n  );\n});\n\nfunction onCheckout() {\n  const currentItem = viewModel.studio.currentItem;\n  if (!currentItem) {\n    return;\n  }\n\n  executor.execute(async () => studio.checkout(currentItem), { loading: true });\n}\nfunction onSave() {\n  const currentItem = viewModel.studio.currentItem;\n  if (!currentItem) {\n    return;\n  }\n\n  executor.execute(async () => studio.save(currentItem), { loading: true });\n}\nfunction onSavePlus() {\n  dialog.showDialog(\n    defineAsyncComponent(() => import(\"../dialogs/studio/savePlus.vue\")),\n    { closable: true }\n  );\n}\n\nasync function release() {\n  executor.execute(async () => studio.release(viewModel.studio.currentItem!), { loading: false });\n}\n\nfunction listJobs(jobType: IExportJob[\"type\"]) {\n  const title = jobType == \"export\" ? \"Export Job List\" : \"Deploy Job List\";\n  dialog.showDialog(\n    defineAsyncComponent(() => import(`../export/exportJobList.vue`)),\n    { closable: true, title, width: \"1000px\", maxHeight: \"500px\", overflow: \"auto\" },\n    { jobType }\n  );\n}\n\nfunction listLivePreviews(){\n  \n  return;\n}\n\nasync function openExportDialog(exportType: ExportType) {\n  executor.execute(\n    async () => {\n      const applicationID = routerParams.appID;\n      selectionData.value = await executor.executeUseCase<IGetExportSelectionData>(IGetExportSelectionData, { applicationID, exportType });\n      dialog.showDialog(\n        ExportApp,\n        {\n          closable: true,\n          title: \"Export: \" + viewModel.studio.appName,\n          image: { src: props.app?.logo || \"../../../../images/is-bankasi-logo.jpg\", width: \"50px\", height: \"50px\" },\n          width: \"1000px\",\n          maxHeight: \"700px\",\n          height: \"635px\",\n          overflow: \"hidden\",\n        },\n        { selectionData: selectionData.value, exportType }\n      );\n    },\n    { loading: true }\n  );\n}\n\nasync function goMainPage() {\n  if(viewModel.studio.openItems && viewModel.studio.openItems.some(item => item.checkedOut && item.modified?.model)) {\n    const result = await notification.showConfirm({\n                type: \"warning\",\n                title: `You are leaving the page.`,\n                text: \"Changes you made may not be saved.\",\n                showCloseIcon: true,\n                approveText: \"Confirm\",\n                cancelText: \"Cancel\",\n            });\n            if (result.result == \"cancel\") {\n                return;\n            }\n  }\n  router.push(\"/main/applicationList\");\n}\n\nfunction checkDeployConditions() {\n  return !viewModel.apps.find((app) => app.ID === viewModel.studio.appId)?.deployParameters?.gitProductRepoId;\n}\n\nfunction logout() {\n  executor.execute(\n    async () => {\n      await executor.executeUseCase<ILogout>(ILogout);\n      router.push(\"/login\");\n    },\n    { loading: true }\n  );\n}\n\nfunction showDeployTip() {\n  if (checkDeployConditions()) {\n    deployTip.value = localization.get(\"deployTip\");\n    return deployTip.value;\n  } else {\n    deployTip.value = \"\";\n    return deployTip.value;\n  }\n}\n\nfunction openDeployScreen(screenType: \"Export\" | \"Deploy\") {\n  dialog.showDialog(\n    defineAsyncComponent(() => import(`../dialogs/deploy/deployScreen.vue`)),\n    { closable: true, title: screenType, width: \"1417px\", height: \"741px\" },\n    { screenType }\n  );\n}\n\nfunction openCustomPreviewSettingsScreen() {\n  dialog.showDialog(\n    defineAsyncComponent(() => import(`../dialogs/preview/customPreviewSettings.vue`)),\n    { closable: true, title: \"Custom Preview Settings\", width: \"800px\", height: \"605px\" }\n  );\n}\n\nasync function openLivePreviewDialogScreen(exportType: ExportType){\n    console.log(\"LivePreviewDialog\");\n    executor.execute(\n        async () => {\n          const applicationID = routerParams.appID;\n          selectionData.value = await executor.executeUseCase<IGetExportSelectionData>(IGetExportSelectionData, { applicationID, exportType });\n          dialog.showDialog(\n            LivePreview,\n            {\n              closable: true,\n              title: \"Live Preview: \" + viewModel.studio.appName,\n              image: { src: props.app?.logo || \"../../../../images/is-bankasi-logo.jpg\", width: \"50px\", height: \"50px\" },\n              width: \"1000px\",\n              maxHeight: \"700px\",\n              height: \"635px\",\n              overflow: \"hidden\",\n            },\n            { selectionData: selectionData.value, exportType}\n          );\n        },\n        { loading: true }\n      );\n}\n\nfunction hoverLogo() {\n  hoverValue.value = true;\n}\nfunction unHover() {\n  hoverValue.value = false;\n}\nfunction deployBtnTooltipOver() {\n  deployTooltipVisbile.value = true;\n  deployContentOpacity.value = 1;\n  timer.reset();\n}\nfunction deployTooltipMouseLeave() {\n  deployTooltipVisbile.value = false;\n}\nfunction deployBtnTooltipMouseLeave() {\n  if (timer.isTimerRunning) {\n    return;\n  }\n  timer.setUp({\n    time: 3000,\n    callback: () => {\n      deployTooltipVisbile.value = false;\n      deployContentOpacity.value = 1;\n      timer.reset();\n    },\n    interval: {\n      time: 1000,\n      callback: () => {\n        deployContentOpacity.value = deployContentOpacity.value - 0.3;\n      },\n    },\n  });\n}\nfunction deployContentpOver() {\n  deployContentOpacity.value = 1;\n  timer.reset();\n}\nfunction openSettings() {\n  deployTooltipVisbile.value = false;\n  dialog.showDialog(\n    defineAsyncComponent(() => import(`../dialogs/deploySettings.vue`)),\n    { closable: true, title: \"Deploy Settings\", width: \"1100px\", height: \"497px\" }\n  );\n}\n\nasync function openLivePreviewTab() {\n  const appID = viewModel.studio.appId;\n\n  await executor.execute(async () => {\n    let lpItem = await livePreviwUseCase.getLivePreviewInfo(appID, { selected: true });\n    if (!lpItem) {\n      await livePreviwUseCase.createLivePreview({ appID, type: \"default\", selected: true });\n    }\n    window.open(`../../livepreview/${appID}`, \"_blank\")\n  }, { loading: true });\n\n  // debugger;\n  // let lpID = getLivePreviewID();\n  // if (!lpID) {\n  //   await executor.execute(async ()=> {\n  //     const appID = viewModel.studio.appId;\n  //     const lpItem = await livePreviwUseCase.createLivePreview({appID, type: \"default\", selected: true });\n  //     lpID = lpItem.ID;\n  //     await appSettingsCase.update({ applicationSettings: { applicationID: appID, livePreviewParameters: { selectedType: \"default\", livePreviews: { defaultID: lpItem.ID } } } });\n  //   }, {loading: true})\n  // }\n\n  // window.open(`../../livepreview/${lpID}`, \"_blank\");\n}\n\nfunction handleStudioOrCustom(selectedType: IAppLivePreviewParameters[\"selectedType\"]) {\n  const currentApp = viewModel.apps.find(app => app.ID == viewModel.studio.appId);\n  if(!currentApp) return;\n  let updateApp = false;\n  if(currentApp?.livePreviewParameters) {\n    if(currentApp.livePreviewParameters.selectedType != selectedType) {\n      currentApp.livePreviewParameters.selectedType = selectedType;\n      updateApp = true;\n    }\n  } else {\n    currentApp!.livePreviewParameters = {selectedType, livePreviews: {}}\n    updateApp = true;\n  }\n  if(updateApp) {\n    delete currentApp?.children\n    executor.execute(\n      async () => {\n        const lpParams = currentApp!.livePreviewParameters!\n        const lpID = lpParams.selectedType == \"default\" ? lpParams.livePreviews.defaultID : lpParams.livePreviews.customID;\n        if (lpID) {\n          await livePreviwUseCase.updateLivePreview(lpID, { selected: true });\n        }\n          await executor.executeUseCase<IUpdateApplication>(IUpdateApplication, currentApp);\n        },\n        { loading: true }\n    )\n  }\n}\n</script>\n<template>\n  <div class=\"top-menu\">\n    <div class=\"row top-container\">\n      <div class=\"col-lg-1 home-section\" style=\"display: flex\">\n        <div class=\"corner-container\">\n          <div class=\"logo-container\" @click=\"goMainPage\" @mouseover=\"hoverLogo()\" @mouseleave=\"unHover()\"\n            :style=\"{ 'background-color': hoverValue ? '#151233' : '', padding: '18px' }\">\n            <img v-if=\"!hoverValue\" src=\"../../../../images/ide5-logo.svg\" alt=\"ide5-logo\" width=\"24\" height=\"24\" />\n            <img v-if=\"hoverValue\" src=\"../../../../images/plateau_hover.svg\" alt=\"ide5-logo\" />\n          </div>\n        </div>\n        <div class=\"vl\"></div>\n      </div>\n\n      <div class=\"col-lg-8 middle-resize\">\n        <div class=\"middle-container\">\n          <div class=\"title-container\">\n            <img src=\"../../../../images/is-bankasi-logo.jpg\" alt=\"is-bankasi-logo\" width=\"24\" height=\"24\"\n              class=\"title-logo\" />\n            <p class=\"title\">{{ viewModel.studio.appName }}</p>\n          </div>\n          <div class=\"tabs\">\n            <Tabs :tabs=\"tablist\" height=\"30px\" ref=\"tabComp\"></Tabs>\n          </div>\n        </div>\n      </div>\n      <div class=\"col-lg-3 last-resize\">\n        <div class=\"end-container position-absolute top-0 end-0\">\n        <!-- <button type=\"button\" class=\"btn-sm end-icons export-button\" id=\"-\" @click=\"openLivePreviewDialogScreen('qui')\"> \n            <i class=\" export\"></i><span class=\"export-text\">▶</span>\n          </button> -->\n\n        <span style=\"margin-top: 5px;\">\n            <div type=\"button\" @click=\"openLivePreviewTab()\" class=\"preview-button\" id=\"dropdownLivePreviewButton\" aria-expanded=\"false\">\n              <span class=\"preview-play-icon\"><i class=\"mdi mdi-play mdi-20px\"> </i></span>\n              <span class=\"pr